$max-width: 20em;

.p2020-searchform {
	overflow: hidden;
	display: flex;
	align-items: center;
	transition: flex-grow 0.6s ease-in-out;
	font-size: pxrem(14);
	max-width: $max-width;

	&::before {
		@include icon("gridicons-search", 1.5em, var(--color-text));
		margin-right: 4px;
	}

	&:focus-within {
		flex-grow: 1;
	}
}

.p2020-searchform__container {
	position: relative;
	display: flex;
}

// To ensure the container is at least as wide as the translated placeholder text
.p2020-searchform__label {
	visibility: hidden;

	@at-root .p2020-searchform:focus-within & {
		display: none;
	}
}

.p2020-searchform__field {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	border: 0;
	font-family: var(--font-default);

	&::placeholder {
		color: var(--color-text);
		opacity: 1;
	}

	&:focus {
		top: -1px;
		flex-grow: 1;
		width: $max-width;
		height: 1.25em; // fix strange collapsing issue in wpcom in Chrome

		&::placeholder {
			opacity: 0.3;
		}
	}
}

