// TODO: Refactor

.navigation {
	margin: 0 0 2em;
	padding: 0;
	overflow: hidden;
	border-top: 1px solid var(--color-border);

	p {
		float: left;
		width: 50%;

		&.nav-newer {
			float: right;
			text-align: right;
		}
	}

	.previous,
	.nav-older {
		float: left;
		margin: 0;
		padding: 0;
		width: 50%;
	}

	.next,
	.nav-newer {
		float: right;
		margin: 0;
		padding: 0;
		text-align: right;
		width: 50%;
	}

	a {
		display: block;
		padding: 1.2em;
		color: var(--color-text-light);
	}

	.nav-older a {
		border-right: 1px solid var(--color-border);
	}

	a:hover {
		color: var(--color-link);
	}

}

/*
 * Keyboard navigation
 */
.keyselected {
	border-left: 2px solid #ff9912;
}

#nav-below {
	clear: both;
	overflow: hidden;
	padding: 15px;
	border-top: 1px solid var(--color-border);

	div {
		float: left;
		width: 50%;
	}

	div.nav-next {
		float: right;
		text-align: right;
	}
}

/* =Navigation
----------------------------------------------- */
@include breakpoint("web") {
	body:not(.custom-menu) {
		#main {
			margin-top: 0 !important;
		}
	}
}
