// TODO: This file hasn't been tested or cleaned yet because we don't support
// cross-posting yet.

.tag-p2-xpost {
	background: transparent;
	padding-bottom: 0;
	margin: -1.5em 0 2em 0;

	.avatar {
		float: left;
		border: 1px rgba($p2020-white, 0.5) solid;
		box-shadow: 0 1px 2px rgba($p2020-black, 0.1);
	}

	.o2-post,
	.o2-comment {
		font-size: 0.9em;
		font-style: italic;
		padding: 10px 10px 10px 0;

		.avatar {
			width: 34px;
		}
	}

	.o2-post {
		background: transparent;
		min-height: 36px;
		padding-left: 10px;
	}

	.author-avatar {
		display: block;

		img {
			margin-right: 10px;
		}
	}

	.o2-comment {
		padding-left: 5em;

		.o2-comment-header {
			overflow: visible;
			padding-bottom: 0;
		}

		.comment-meta {
			line-height: 1.4;
		}

		.o2-actions {
			display: none;
		}
	}

	.o2-tags-array,
	.sharedaddy {
		display: none;
	}

	p {
		margin: 0;
	}

	.comment-likes {
		display: none;
	}

}

article.tag-p2-xpost {
	.comment {
		.avatar {
			margin-left: -10px;
			margin-top: 0;
		}
	}
}

.o2-xpost-author {
	font-size: 1em;
	overflow: hidden;

	a {
		padding: 0;
	}

	.entry-author {
		font-size: 1em;
	}

	.entry-date {
		color: var(--color-text-light);
		font-size: 0.9em;
		font-weight: 300;
		padding-left: 5px;
	}
}

.o2-xpost-avatar {
	border-width: 0;
	box-shadow: none;
}

.o2-xpost-entry-date {
	float: none;

	&:hover {
		color: var(--color-link);
	}
}

@include breakpoint( "handset-portrait" ) {
	.tag-p2-xpost {
		margin: 0 0 0.5em !important;
	}

}

.o2-post .o2-xpost-content p {
	margin-top: 0;
	margin-bottom: 0;
}
