#page {
	position: relative;
}

#masthead {
	position: relative;
	z-index: 1;
}

#main-wrapper {
	display: flex;
}

#content {
	position: relative;
	z-index: 0;
	margin: 0 auto;
	width: 75%;
	max-width: 940px;
	padding: 50px;

	@include breakpoint( "tablet-portrait" ) {
		width: 100%;
		padding: 16px;
	}
}

#sidebar {
	width: 25%;
	max-width: 360px;
	min-width: 280px;
	border-right: 1px solid var(--color-border);

	@include breakpoint( "tablet-portrait" ) {
		position: absolute;
		top: 0;
		left: -200%;
		right: 0;
		z-index: 1;
		transition: left 0.25s ease-in-out;
		width: 100%;
		margin: 12px auto;
		border: 0;
		border-radius: 4px;
		background: var(--color-white);
		box-shadow: $shadow-popover;

		@at-root .mobile-menu-is-visible & {
			left: 0;
		}
	}
}

@include breakpoint( "tablet-portrait" ) {
	// Sidebar is showing
	.mobile-menu-is-visible {
		#o2-expand-editor {
			display: none;
		}
	}
}
