.widget {
	font-size: pxrem(14);
	line-height: 1.4;

	& + & {
		margin-top: 3em;
	}

	select {
		max-width: 100%;
	}
}

.widget-title {
	margin-bottom: 1em;
	font-size: 1.3em;
	font-weight: 500;
}

/**
 * Core widgets
 */
.widget_media_image,
.widget_custom_html {
	img {
		display: block;
		border-radius: 4px;
	}
}

.widget_calendar {
	.wp-calendar-table {
		margin-bottom: 8px;

		caption { /* Month & Year */
			font-size: 1.1em;
			padding: 0.4em;
			text-align: center;
		}

		th,
		td {
			text-align: center;
			padding: 0.3em;
		}
	}
}

.widget_recent_comments {
	.recentcommentstexttop,
	.recentcommentstextend {
		vertical-align: middle;
	}
}

/**
 * P2020 widgets
 */

.widget_p2020-my-team-widget {
	.widget-myteam-team-count {
		display: inline-block;
		font-size: 0.6em;
		font-weight: 400;
		margin-left: 0.6em;
	}

	#widget-myteam-manage-link {
		float: right;
		font-size: 0.6em;
		font-weight: 400;
		margin-top: 0.7em;
	}

	#widget-myteam-manage-icon {
		border: 1px solid #d0cdcd;
		border-radius: 100%;
		cursor: pointer;
		display: inline-block;
		font-size: 1.5em;
		font-weight: 400;
		height: 48px;
		line-height: 48px;
		padding: 0;
		text-align: center;
		width: 48px;

		&.has-more {
			font-size: 1em;
			padding: 0 2px 0 0;
		}

		a {
			color: var(--color-text-light);
		}
	}
}

@mixin widget-p2020-filter-gridicon($id-slug, $icon-name) {
	#widget-filter-#{$id-slug} .widget-filter-item-link-icon::before {
		@include icon("custom-#{$icon-name}", pxrem(24), currentColor);
	}
}

.widget_p2020-filter-widget {
	#widget-filter-me {
		margin-top: pxrem(40);
	}

	.widget-filter-me-title {
		margin-bottom: pxrem(12);
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-end;
	}

	.widget-filter-me-label {
		display: inline-block;
		font-size: pxrem(18);
		font-weight: 500;
	}

	#widget-filter-me-settings {
		font-size: pxrem(12);
		color: var(--color-text-light);

		&:hover {
			color: var(--color-link);
		}
	}

	.widget-filter-item {
		color: var(--color-text);
		line-height: 1.3;
		margin: 0 pxrem(-10);
		padding: pxrem(1) 0;

		.widget-filter-item-link {
			color: inherit;
			display: flex;
			align-items: center;
			padding: pxrem(6) pxrem(10);
			border-radius: 4px;

			&:hover {
				background: var(--color-background-light);
			}
		}

		&.is-active {
			.widget-filter-item-link {
				color: var(--color-link);
				background: var(--color-background-link);
			}

		}

		.widget-filter-item-link-icon {
			margin-right: 0.5em;
			display: flex;
		}

		.widget-filter-item-link-label {
			flex: 2;
			vertical-align: top;
			font-size: pxrem(13);
			font-weight: 500;
		}

		.widget-filter-item-link-unread {
			justify-self: flex-end;
			border: 1px solid var(--color-border);
			background: var(--color-background-very-light);
			border-radius: 100px;
			font-size: pxrem(11);
			font-weight: 500;
			padding: 0.2em 0.8em;
		}
	}

	// Icons
	@include widget-p2020-filter-gridicon("recent-updates", "posts");
	@include widget-p2020-filter-gridicon("recent-comments", "comment");
	@include widget-p2020-filter-gridicon("my-mentions", "mention");
	@include widget-p2020-filter-gridicon("my-posts", "user");
}
