.o2-app-controls {
	visibility: hidden;
	float: none;
	display: flex;
	justify-content: flex-end;
	margin-top: 24px;
	font-weight: 500;
	font-size: pxrem(12);

	.o2-toggle-comments {
		visibility: visible;
		order: 3;
		color: var(--color-text-light);
	}

	@include breakpoint("tablet-portrait") {
		display: none;
	}
}
