%p2020-article {
	overflow-wrap: break-word;
	margin: 0 0 30px;
	border-radius: 4px;
	background: var(--color-white) !important; // override o2 making newly posted articles transparent
	box-shadow: $shadow-card;
	font-size: $body-font-size;

	&.sticky {
		border: 3px solid var(--color-link);

		&::before {
			@include icon("custom-tack", 24px, var(--color-link));

			position: absolute;
			margin-left: 24px;
			margin-top: 24px;

			@include breakpoint("handset-portrait") {
				display: none;
			}
		}
	}

	/**
	 * Images
	 */
	.alignfull {
		@include breakout-container-padding-responsive(
			$horizontal-padding-default,
			$horizontal-padding-narrow,
			"handset-portrait"
		);
	}
}
